import React from 'react'
import s from './Post.module.css'

const Post = (props) => {
	return (
	<div className={s.posts}>
		<div className={s.aboutMe}> {props.message}</div>
		<img className={s.ava} src='data:image/jpeg;base64,'/>
		<div className={s.aboutMe}>{props.countLike} лайков</div>
	</div>
	)
}

export default Post